import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

const cssNoMin = css; // html-minifier-terser fail!

export const splitLayoutCellStyles: GenerateStyles = (tagName) => {
    const { child, root, is } = cssSelector(tagName);

    return [
        style,
        cssNoMin`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                --gutter-size: 4px;
                --gutter-adjust: -2px;
                transition: margin-left 0.25s ease-in-out, margin-right 0.25s ease-in-out,
                    margin-bottom 0.25s ease-in-out, margin-top 0.25s ease-in-out;
            }

            :host(:not([drag])) {
                transition: width 0.25s ease-in-out, margin-left 0.25s ease-in-out, margin-right 0.25s ease-in-out,
                    margin-bottom 0.25s ease-in-out, margin-top 0.25s ease-in-out;
            }

            :host([dimension='width']:not([main])) {
                height: 100%;
                width: var(--size);
            }
            :host([dimension='height']:not([main])) {
                width: 100%;
                height: var(--size);
            }
            :host([main]) {
                flex: 1;

                min-width: 60px;
            }

            ::slotted(*) {
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            ${root} {
                width: 100%;
                height: 100%;
                position: relative;
            }

            ${child('inner')} {
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            ${is('drag')} ${child('inner')} {
            }

            ${is('disabled')} {
                display: none;
            }

            ${child('gutter')} {
                position: absolute;
                transition: opacity 0.25s ease-in;
                bottom: 0;
                right: 0;
                z-index: 1;
                opacity: 0;
                background-color: ${cssVariableAsVar('color_primary_1')};
            }
            ${is('!resize')} ${child('gutter')} {
                display: none;
            }

            ${child('gutter-img')} {
                background-repeat: no-repeat;
                background-position: 50% center;
                height: 100%;
                width: 100%;
                opacity: 0.25;
            }
            :host([dimension='width']) ${child('gutter-img')} {
                background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAAXNSR0IArs4c6QAAAChJREFUKFNjZGBg+M8AAYwwNpyBLghViKBAKrEKjpo5Gp7gNIA1LQEAhwwgD7JA85wAAAAASUVORK5CYII=);
            }
            :host([dimension='height']) ${child('gutter-img')} {
                background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB4AAAAFCAYAAABSIVz6AAAAAXNSR0IArs4c6QAAAChJREFUKFNjZGBg+M8AAYxQmi58kGV0sQjdYzBfQj1LP2pAfTwgQQ0A5uMgAl9UA0gAAAAASUVORK5CYII=');
            }

            :host([dimension='width']) ${child('gutter')} {
                height: 100%;
                width: var(--gutter-size);
                right: var(--gutter-adjust);
            }
            :host([dimension='width']:not([rear])) ${child('gutter')} {
                right: var(--gutter-adjust);
            }
            :host([dimension='width'][rear]) ${child('gutter')} {
                left: var(--gutter-adjust);
            }

            :host([dimension='height']) ${child('gutter')} {
                width: 100%;
                height: var(--gutter-size);
            }
            :host([dimension='height']:not([rear])) ${child('gutter')} {
                bottom: var(--gutter-adjust);
            }
            :host([dimension='height'][rear]) ${child('gutter')} {
                top: var(--gutter-adjust);
            }

            ${child('gutter')}:hover,
            ${child('gutter-drag')} {
                opacity: 1;
            }

            :host([dimension='width']) ${child('gutter')}:hover {
                cursor: ew-resize;
            }

            :host([dimension='height']) ${child('gutter')}:hover {
                cursor: ns-resize;
            }

            :host([dimension='width'][close]:not([rear])) {
                margin-left: calc(-1 * var(--size));
            }
            :host([dimension='width'][close][rear]) {
                margin-right: calc(-1 * var(--size));
            }
            :host([dimension='height'][close]:not([rear])) {
                margin-top: calc(-1 * var(--size));
            }
            :host([dimension='height'][close][rear]) {
                margin-bottom: calc(-1 * var(--size));
            }
        `,
    ];
};
