import { BaseElement, booleanConverter, defineElement, html, property, state } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { SplitLayoutProperties } from './splitLayoutProperties';
import { splitLayoutStyles } from './splitLayoutStyle';
import { SplitLayoutCell } from './splitLayoutCell';

@defineElement({ tagName: 'split-layout', styles: splitLayoutStyles, version })
export class SplitLayout extends BaseElement implements SplitLayoutProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    vertical;
    @property({ type: String, attribute: 'gutter-size' })
    gutterSize;

    @state()
    private _cells: SplitLayoutCell[] = [];

    constructor() {
        super();
        this._slotChangeHandler = this._slotChangeHandler.bind(this);
    }

    connectedCallback() {
        super.connectedCallback();
        this.addEventListener('slotchange', this._slotChangeHandler);
    }

    disconnectedCallback() {
        this.removeEventListener('slotchange', this._slotChangeHandler);
        super.disconnectedCallback();
    }

    protected updated(changes): void {
        super.updated(changes);
        if (changes.size > 0) {
            this._cells.forEach((c) => {
                c.dimension = this.vertical ? 'height' : 'width';
                if (this.gutterSize) {
                    c.gutterSize = this.gutterSize;
                }
            });
        }
    }

    protected render() {
        return html` <slot @slotchange="${this._slotChangeHandler}"></slot>`;
    }

    private _slotChangeHandler(e) {
        const target = e.target as HTMLSlotElement;
        if (target.assignedNodes) {
            this._cells = target.assignedNodes().filter((node) => node.nodeType === 1 && node) as SplitLayoutCell[];
        }
    }
}
