import { GenerateStyles, style, css } from '@umetrics/sartorius-ui-base';

export const splitLayoutStyles: GenerateStyles = () => {
    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: flex;
                flex-direction: row;
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            :host([vertical]) {
                flex-direction: column;
            }
        `,
    ];
};
