import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { splitLayoutCellStyles } from './splitLayoutCellStyle';
import { Dimension, SplitLayoutCellProperties } from './splitLayoutCellProperties';
import { SplitLayoutEvent } from './splitLayoutEvent';
import { EventDispatcherMixin } from '@umetrics/sartorius-ui-mixins';

@defineElement({ tagName: 'split-layout-cell', styles: splitLayoutCellStyles, version })
export class SplitLayoutCell extends EventDispatcherMixin(BaseElement) implements SplitLayoutCellProperties {
    // Static members
    private static cacheWidth = { start: 'left', end: 'right', client: 'clientX', cursor: 'ew-resize' };
    private static cacheHeight = { start: 'top', end: 'bottom', client: 'clientY', cursor: 'ns-resize' };

    // Private members

    // Protected members

    // Public members
    @property({ type: Number, reflect: false })
    size = 150;
    @property({ type: String, reflect: true })
    dimension: Dimension = 'width';

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    main = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'no-resize' })
    noResize = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    close = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    rear = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    drag = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    disabled = false;

    @property({ type: Number, attribute: 'gutter-size' })
    gutterSize = 6;
    @property({ type: Number, attribute: 'gutter-adjust' })
    gutterAdjust = -3;

    @property({ type: Number, attribute: 'min-size' })
    minSize = 50;
    @property({ type: Number, attribute: 'max-size' })
    maxSize = 0;
    @property({ type: Number, attribute: 'snap-size' })
    snapSize = 20;
    @property({ type: Boolean, attribute: 'snap-close', converter: booleanConverter })
    snapClose = false;

    private _dragOffset = -1;
    private _dragRect;
    private _cache = SplitLayoutCell.cacheWidth;

    constructor() {
        super();
        this._gutterStartDrag = this._gutterStartDrag.bind(this);
        this._mouseMoveHandler = this._mouseMoveHandler.bind(this);
        this._mouseUpHandler = this._mouseUpHandler.bind(this);
    }

    protected updated(_changedProperties) {
        super.updated(_changedProperties);
        if (_changedProperties.has('dimension')) {
            this._cache = this.dimension === 'height' ? SplitLayoutCell.cacheHeight : SplitLayoutCell.cacheWidth;
        }
        if (
            _changedProperties.has('size') ||
            _changedProperties.has('gutterSize') ||
            _changedProperties.has('gutterAdjust')
        ) {
            this._setCssProperty();
        }
        const prevClose = _changedProperties.get('close');
        if (prevClose !== undefined) {
            if (prevClose !== this.close) {
                this.addEventListener('transitionend', () => this._onTransitionEnd(), { once: true });
            }
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            resize: !this.noResize,
            drag: this.drag,
            disabled: this.disabled,
        });

        const classList = `${baseClassName}-gutter` + (this.drag ? ` ${baseClassName}-gutter-drag` : '');
        const gutter = this.noResize
            ? null
            : html` <div class="${classList}" @pointerdown="${this._gutterStartDrag}">
                  <div class="${baseClassName}-gutter-img" />
              </div>`;

        return html` <div class="${rootClasses}">
            <div class="${baseClassName}-inner">
                <slot></slot>
                ${gutter}
            </div>
        </div>`;
    }

    /**
     * Transition end (css) event handler after toggling close-state
     *
     * @private
     */
    private _onTransitionEnd() {
        this.dispatch(this.close ? SplitLayoutEvent.CellClose : SplitLayoutEvent.CellOpen);

        if (!this.close && this._dragRect) {
            this.size = this._dragRect.width;
        }
    }

    private _gutterStartDrag(e) {
        e.preventDefault();

        this._dragRect = this.getBoundingClientRect();
        this._dragOffset =
            this._getMousePosition(e) -
            (this.rear ? this._dragRect[this._cache.start] : this._dragRect[this._cache.end]);

        document.addEventListener('pointermove', this._mouseMoveHandler);
        document.addEventListener('pointerup', this._mouseUpHandler);
        document.body.style.cursor = this._cache.cursor;

        this.drag = true;
    }

    private _mouseMoveHandler(e) {
        let offset;
        if (this.rear) {
            offset = this._dragRect[this._cache.end] - this._getMousePosition(e) + this._dragOffset;
        } else {
            offset = this._getMousePosition(e) - this._dragRect[this._cache.start] + this._dragOffset;
        }

        const totGutter = this.gutterSize + this.gutterAdjust;
        const largestMinSize = this.minSize > totGutter ? this.minSize : totGutter;

        if (offset <= this.minSize) {
            this.size = largestMinSize;
        } else if (offset + this.gutterSize < this.minSize + this.snapSize + this.gutterAdjust) {
            if (this.snapClose && !this.close) {
                this.close = true;
                this._dragEnd();
            }
            this.size = largestMinSize;
        } else if (this.maxSize > 0 && (offset >= this.maxSize || offset >= this.maxSize - this.snapSize)) {
            this.size = this.maxSize;
        } else {
            this.size = offset;
        }
        this._setCssProperty();
    }

    private _mouseUpHandler() {
        this._dragRect = undefined;
        this._dragEnd();
        this.dispatch(SplitLayoutEvent.CellResize, { size: this.size });
    }

    private _dragEnd() {
        document.removeEventListener('pointermove', this._mouseMoveHandler);
        document.removeEventListener('pointerup', this._mouseUpHandler);
        document.body.style.cursor = 'default';
        this.drag = false;
        window.dispatchEvent(new CustomEvent('resize', { bubbles: true }));
    }

    private _getMousePosition(e) {
        return 'touches' in e ? e.touches[0][this._cache.client] : e[this._cache.client];
    }

    private _setCssProperty() {
        this.style.setProperty('--size', `${this.size}px`);
        this.style.setProperty('--gutter-size', `${this.gutterSize}px`);
        this.style.setProperty('--gutter-adjust', `${this.gutterAdjust}px`);
    }
}
